    height: 70px;
    margin-left: 30px;
    margin-top: 25px;
  }
  .secondprofilecolumn-change {
    font-size: 20px;
    margin-top: 29px;
  }
  .firstprofilecolumn-lastprice {
    font-size: 12px;
  }
  .firstprofilecolumn {
    margin-left: 20px;
  }
  .secondary-nav {
    padding: 5px;
  }
  .secondary-nav a {
    font-size: 18px;
    width: 145px;
    text-align: center;
  }

  .secondary-nav a {
    color: white;
    cursor: pointer;
    text-decoration-line: none;
    padding: 8px;
    border-radius: 3px;
    outline: none;
  }

  .secondary-nav a:hover {
