  }
  .statements {
    padding: 16px 20px;
    margin-right: 0;
    width: 550px;
  }
  .statements-heading {
    font-size: 20px;
  }
  .bsstatements {
    padding: 16px 20px;
    margin-right: 0;
    width: 550px;
  }
  .bsstatements-heading {
    font-size: 20px;
  }
  .cfstatements {
    padding: 16px 20px;
    margin-right: 0;
    width: 550px;
  }
  .cfstatements-heading {
    font-size: 20px;
  }
  .statements-table th,
  td {
    font-size: 16px;
  }
  .statement-graph {
    padding: 16px 20px;
    margin-left: 0;
    width: 830px;
  }
  .bs-statement-graph {
    padding: 16px 20px;
    width: 830px;
    margin-left: 0;
  }
  .cf-statement-graph {
    padding: 16px 20px;
    width: 830px;
    margin-left: 0;
  }
  .description {
    padding: 16px 20px;
  }
}
