/* ------------ *\
    Close icon
\* ------------ */
.close-icon {
  display: inline;
  position: absolute;
  padding: 18px 0px;
  right: 30px;
  cursor: pointer;
}
.close-icon > span {
  position: relative;
}
.close-icon > span::before,
.close-icon > span::after {
  content: "";
  height: 2px;
  width: 16px;
  position: absolute;
  display: inline-block;
  background-color: #ffffff;
  opacity: 70%;
}
.close-icon > span::after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close-icon > span::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

