.stockspecific-table-wrapper {
  width: auto;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  border-radius: 4px;
  overflow-x: scroll;
  margin-left: 8px;
  border: 1px solid var(--background-color);
}

/* hidding the scrollbar */
.stockspecific-table-wrapper::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.stockspecific-table,
th,
td {
  border-collapse: collapse;
  padding: 12px;
  background-color: var(--section-color);
}

/* adding column name properties */
.stockspecific-table th {
  font-weight: 800;
  background-color: var(--background-color);
  white-space: nowrap;
}

/* background color to heading row of table */
.stockspecific-table tr:first-of-type {
  background-color: var(--background-color);
}

/* aligning row data items except ticker to right */
.stockspecific-table tr:not(:first-child) td:not(:first-child) {
  text-align: right;
}

/* making first column fixed to the table */
.stockspecific-table th:first-child,
.stockspecific-table td:first-child {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  min-width: 250px;
  max-width: 250px;
  box-shadow: 2px 0px 2px -1px rgba(0, 0, 0, 0.2);
}

/* refactoring text of ticker company */
.ticker-company {
  font-size: 12px;
  opacity: 70%;
  white-space: wrap;
}

/* adding horizontal line after every row */
.stockspecific-table tr:not(:last-of-type)::after {
  content: "";
  width: 98%;
  height: 1px;
  background-color: var(--background-color);
  /* border-bottom: 1px solid var(--background-color); */
}

/* -------------------- *\
    Screener Component
\* -------------------- */

/* Filter FAB (Floating action button) */
#filter-fab {
  position: fixed;
  right: 24px;
  bottom: 24px;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: var(--primary-color);
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2),
    0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
}

/* Filter FAB icon */
#filter-fab > span,
#filter-fab > span::before,
#filter-fab > span::after {
  background-color: #ffffff;
  height: 2px;
  display: inline-block;
}
#filter-fab > span {
  position: relative;
  width: 18px;
  top: 1px;
}
#filter-fab > span::before,
#filter-fab > span::after {
  position: absolute;
  content: "";
}
#filter-fab > span::before {
  width: 26px;
  top: -6px;
  left: -4px;
}
#filter-fab > span::after {
  width: 10px;
  top: 6px;
  left: 4px;
}

/* ------------------ *\
    Filter container
\* ------------------ */

.filter-container {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 4;
}

.filter-modal {
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 0;
  z-index: 5;
  width: calc(100vw - 20px);
  height: calc(100vh - 80px);
  background-color: var(--section-color);
  border-radius: 8px 8px 0px 0px;
}

/* --------------- *\
    Filter header
\* --------------- */

.filter-header {
  padding: 12px 16px;
}
#filter-back-icon {
  cursor: pointer;
}
hr {
  width: 90%;
  border: 0px;
  align-self: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.filter-header .close-icon {
  padding: 0px;
  top: 20px;
  display: flex;
  align-items: center;
}

.filter-header .arrow.left {
  margin-right: 12px;
}
.arrow {
  border: solid #ffffff;
  border-width: 0 2px 2px 0px;
  display: inline-block;
  padding: 4px;
}

.left {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.right {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

/* Filter Items */

.filter-items {
  margin: 16px 24px 0px 24px;
  overflow-y: scroll;
}

.filter-items .search-box {
  padding: 12px 16px;
}

.filter-items div {
  cursor: pointer;
}

.selectable-button {
  padding: 12px 16px;
  background-color: var(--background-color);
  border-radius: 4px;
  display: flex;
  align-items: center;
}

.selectable-button:not(:last-of-type) {
  margin-bottom: 14px;
}

.selectable-button .right {
  margin-left: auto;
}

/* -------------- *\
    Filter Items
\* -------------- */

.filter-items-list {
  margin: 14px 0px;
}

.checkbox-li {
  list-style: none;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.checkbox-li > input {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 10px;
  margin-top: 0px;
}

/* Button */
.apply-button {
  cursor: pointer;
  padding: 10px 16px;
  width: 90%;
  align-self: center;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 14px;
